.movie {
}
.content__container {
  object-fit: contain;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 90%;
  padding-bottom: 20px;
  width: 100%;

  scrollbar-width: none;
  background-color: #39445a;
  color: #fff;
}
/* хром, сафари */
.content__container::-webkit-scrollbar {
  width: 0;
}
/* ie 10+ */
.content__container {
  -ms-overflow-style: none;
}
/* фф (свойство больше не работает, других способов тоже нет)*/
.content__container {
  overflow: -moz-scrollbars-none;
}

.content__poster {
  border-radius: 10px;
  height: 20px;
  width: 100%;
  position: relative;
  /*padding: 0 0 150px 0;*/
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
}
.back__poster {
  position: absolute;
  width: 100%;
  height: 90%;
  left: 0%;
  top: 5%;
  /*justify-self: flex-start;*/
  /*align-self: start;*/
  border-radius: 10px;
  object-fit: cover;
  /*width: 100%;*/

  /*object-fit: cover;*/
}

.content__about {
  padding: 0px 5px;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.content__title {
  /*display: inline-block;*/
  font-weight: 700;
  font-size: 1.2em;
  letter-spacing: 0.75px;
}

.content__tag {
  padding: 15px 5px;
}
.content__tagline {
  align-self: center;
  padding-bottom: 10px;
}

.content__description {
  padding: 15px 5px;
  /*flex: 1 1 auto;*/
  /*box-shadow: inset 0 0 2.5px #000000;*/
  text-align: justify;
  border-radius: 10px;
}
.content__carusel {
}

.alice-carousel__stage-item {
  max-width: 8rem;
}

.carousel__item {
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 5px 10px;
  max-height: 12rem;
}
.carousel__item_img {
  border-radius: 10px;
  object-fit: cover;
  margin-bottom: 3px;
  overflow: hidden;
  box-shadow: 0px 0px 5px black;
}
.carousel__item_text {
  font-size: 0.85em;
  line-height: 1em;
  text-transform: capitalize;
  font-weight: 400;
}

@media screen and (max-width: 991px) {
  .carousel__item {
    max-height: 10rem;
  }
}

